#title {
    text-align: center;
    background-color: rgba(247, 238, 228, .8);
    width: 80vw;
    margin-left: 10vw;
    margin-right: 10vw;
    border-radius: 10px;
    height: 40vh;
    margin-top: 5vh
}

.container {
    margin-top: 2vh;
    margin-bottom: 0vh;
    width: 40vw;
    height: 20vh;
    -webkit-transition: .4s;
    transition: .4s;
}

.form-control, #nav-title, .btn-success, #title, .panel-default {
    opacity: .85;

}
.styleBox {
    position: relative;
    top: 25%;
}
.btn-success {
    float: left;
}

#nav-title {
    font-family: 'Source Code Pro', monospace;
    font-size: 90px;
    letter-spacing: 5px;
}
h2 {
    font-family: Arial, Helvetica, sans-serif;
    font-size: 20px;
    letter-spacing: 2px;
}

nav {
    text-align: center;
    margin-top: 10px;
    letter-spacing: 2px;
}

.linkHome, .linkDash, .linkMng {
    color: black;
    -webkit-transition: 0.4s;
    transition: 0.4s;
    margin-right: 5px;
    border-radius: 2px;
    text-decoration: none;
}
.linkHome:link, .linkDash:link, .linkMng:link { 
    text-decoration: none;
}
.linkHome:visited, .linkDash:visited, .linkMng:visited { 
    text-decoration: none;
}
.linkHome:hover, .linkDash:hover, .linkMng:hover { 
    background: #faf4ee4d;
    -webkit-transition: 0.4s;
    transition: 0.4s;
    text-decoration: none;

  }

#dashboard-title {
    text-align: center;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: bold;
    font-size: 20px;
    letter-spacing: 3px;
}

#manage-title {
    text-align: center;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: bold;
    font-size: 20px;
    letter-spacing: 3px;
}

#guest-title {
    font-family: Arial, Helvetica, sans-serif;
    font-size: 20px;
    letter-spacing: 2px;
}

#modal-message {
    font-size: 18px;
    text-align: center;
    letter-spacing: 2px;
}


@media only screen and (max-width: 600px) {
    .container {
        position: relative;
        top:10vh;
        width: 70vw;
    }
    #title {
        position: relative;
        top: 10vh;
        height: 30vh;
        font-size: 20px;
    }
    #nav-title{
        font-size: 60px;
    }
    h2 {
        margin-bottom: 10%;
        font-family: Arial, Helvetica, sans-serif;
        font-size: 12px;
        letter-spacing: 2px;
    }
  }
/* ---------------------------DEFAULT BACKGROUND COLORS OF PALLETTE */

body {
    background: white;
}

.circle-picker {
    width:100px;
    position: fixed;
    margin-right: 0px;
    padding-left: 12px;
    padding-top: 12px;
    
}
.circle-picker-container {
    position: fixed;
    left: -195px;
    top: 50px;
}

.circle-picker-primary, .circle-picker-secondary, .circle-picker-font {
    position: absolute;
    left: 0px;
    top: 100px;
    -webkit-transition: 0.3s;
    transition: 0.3s
}

.buttonBox {
    position: absolute;
    left: 5px;
    top: 10px;
}
.buttonPrimary, .buttonSecondary, .buttonFont {
    position: absolute;
    -webkit-transition: 0.3s;
    transition: 0.3s;
    width: 80px;
    border-radius: 25px;
    border-width: 1px;
    border-color: black;
}
.buttonPrimary {
    top:0px;
}
.buttonSecondary {
    top: 30px
}
.buttonFont {
    top: 60px
}
.MenuContainer {
    display: inline-block;
    cursor: pointer;
    position: fixed;
    left: 10px;
    top: 10px;
}
  
.bar1, .bar2, .bar3 {
    width: 35px;
    height: 5px;
    background-color: #333;
    margin: 6px 0;
}
.btn-success {
    font-size: 15px;
    font-weight: bold;
}
/* ------------------------------------------ANIMATIONS AND STATE CHANGES */
body, #title, .bar1, .bar2, .bar3, .circle-picker-container, .panel-default, .form-control, .buttonBox, .btn-success {
    -webkit-transition: 0.3s;
    transition: 0.3s;
}
.bar1change {
    -webkit-transform: rotate(-45deg) translate(-9px, 6px);
    transform: rotate(-45deg) translate(-9px, 6px);
}

.bar2change {
    opacity: 0;
}

.bar3change {
    -webkit-transform: rotate(45deg) translate(-8px, -8px);
    transform: rotate(45deg) translate(-8px, -8px);
}
.circleChange {
    -webkit-transform: translate(200px, 0px);
    transform: translate(200px, 0px);
}

.invisible {
    visibility: hidden;
}




